.main-nav {
  max-width: 100%;
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @media (min-width: $desktop-breakpoint) and (max-width: 1423px) {
    padding-left: 20px;
  }
}

.main-nav__logo {
  padding-left: 13px;
  margin-right: auto;

  @media (min-width: $desktop-breakpoint) and (max-width: 1423px) {
    padding-left: 0;
    margin-right: 0;
  }

  @media (max-width: $tablet-breakpoint) {
    padding-left: 10px;
  }

  @media (max-width: $mobile-breakpoint) {
    padding-left: 16px;
  }
}

.main-nav__logo-desk {
  @media (max-width: $mobile-breakpoint) {
    display: none;
  }
}

.main-nav__logo-mobile {
  display: none;

  @media (max-width: $mobile-breakpoint) {
    display: block;
  }
}

.main-nav-items {
  list-style: none;
  margin: 0;
  padding-top: 15px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  flex-wrap: wrap;
  min-width: 470px;

  @media (min-width: $desktop-breakpoint) and (max-width: 1423px) {
    width: 580px;
  }

  @media (max-width: $tablet-breakpoint) {
    min-width: 83px;
    padding-right: 8px;
    padding-left: 0;
  }

  @media (max-width: $mobile-breakpoint) {
    padding-top: 5px;
    padding-right: 46px;
    padding-left: 0;
    min-width: 72px;
  }
}

.main-nav-items li {
  margin-right: 85px;

  @media (max-width: $tablet-breakpoint) {
    margin-right: 33px;
  }

  @media (max-width: $mobile-breakpoint) {
    margin-right: 23px;
  }

  &:last-child {
    margin-right: 0;
  }
}

.main-nav-items a {
  font-family: $main-text-font;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  color: $white-text-color;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 1;
  }
}

.main-nav-items svg {
  display: inline;
  vertical-align: baseline;
  width: 18px;
  height: 18px;

  @media (max-width: $tablet-breakpoint) {
    width: 25px;
    height: 25px;
  }

  @media (max-width: $mobile-breakpoint) {
    width: 25px;
    height: 25px;
  }
}

.main-nav-items span {
  padding-left: 10px;

  @media (max-width: $tablet-breakpoint) {
    display: none;
  }
}

.main-nav__call {
  display: block;
  font-family: $main-header-font;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $white-text-color;
  width: 180px;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 0;
  background: transparent;
  border: 1px solid #ffffff;
  border-radius: 4px;
  margin-top: 5px;
  margin-left: 27px;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 1;
  }

  @media (min-width: $desktop-breakpoint) and (max-width: 1423px) {
    margin-left: 0;
    margin-right: 20px;
  }

  @media (max-width: $tablet-breakpoint) {
    display: none;
  }
}
